<template>
  <el-tabs v-model="activeName" @tab-click="handleClick">
    <el-tab-pane label="每日对账" name="first">
      <div class="p_6">
        <div class="search-wrapper">
          <div class="search-content">
            <el-select v-model="searchParam.device" slot="prepend" placeholder="手机设备" style="width: 100px;">
              <el-option
                v-for="item in deviceOptions"
                :key="item.value"
                :label="item.name"
                :value="item.value">
              </el-option>
            </el-select>
            <el-date-picker
              v-model="searchParam.dateRange"
              type="daterange"
              placeholder="选择日期范围">
            </el-date-picker>
          </div>
          <div class="search-btn">
            <el-button slot="append" icon="search" @click="search" style="width:80px; ">查询</el-button>
          </div>
        </div>
        <el-table
          :data="tableData" style="width: 100%" border :default-sort="{prop: 'date', order: 'descending'}">
          <el-table-column
            type="index"
            align="center"
            width="50">
          </el-table-column>
          <el-table-column prop="date" label="日期" align="center" min-width="120" sortable></el-table-column>
          <el-table-column prop="loanAmount" label="正常还款本金" align="center" width="80" sortable></el-table-column>
          <el-table-column prop="prepaymentCapital" label="正常还款利息" align="center" min-width="150"
                           sortable></el-table-column>
          <el-table-column prop="prepaymentInterest" label="提前还款本金" align="center" width="100"
                           sortable></el-table-column>
          <el-table-column prop="advancePrepayment" label="提前还款利息" align="center" min-width="150"
                           sortable></el-table-column>
          <el-table-column prop="advanceInterest" label="现金金额" align="center" min-width="120"
                           sortable></el-table-column>
          <el-table-column prop="cashBalance" label="累计已收到利息" align="center" min-width="120" sortable></el-table-column>
          <el-table-column prop="receivedInterest" label="未到期本金余额" align="center" min-width="120"
                           sortable></el-table-column>
          <el-table-column prop="outstandingPrincipalBalance" label="未到期利息公允价值" align="center" min-width="120"
                           sortable></el-table-column>
          <el-table-column prop="outstandingInterest" label="理财人公允价值金额" align="center" min-width="120"
                           sortable></el-table-column>
        </el-table>
        <div class="pagination_position">
          <el-pagination
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :current-page="currentPage"
            :page-sizes="pageSizes"
            :page-size="pageSize"
            layout="total, sizes, prev, pager, next, jumper"
            :total="total">
          </el-pagination>
        </div>
      </div>
    </el-tab-pane>
    <div v-if="showDetail">
      <el-tab-pane name="second">
      </el-tab-pane>
    </div>
  </el-tabs>
</template>

<style scoped>
  .el-select .el-input {
    width: 100px;
  }

  .pagination_position {
    padding-top: 20px;
  }

  .echarts {
    height: 300px;
  }
</style>
<style>
  .el-table .cell, .el-table th > div {
    padding-left: 5px;
    padding-right: 5px;
    box-sizing: border-box;
    text-overflow: ellipsis;
  }
</style>
<script>
  import Common from '../../../utils/common'
  import {Notification} from 'element-ui';
  import formatDate from '../../../utils/formatDate.js'

  export default{
    data(){
      let date = new Date();
      let prevMonth = new Date(date.setDate(date.getDate() - 30));
      let now = new Date();

      return {
        deviceOptions: [{
          value: 'ALL',
          name: 'ALL',
        }, {
          value: 'iPhone',
          name: 'iOS',
        }, {
          value: 'Android',
          name: 'Android'
        }],
        detailData: {},
        searchParam: {
          device: "ALL",
          dateRange: [now, now],
        },
        activeName: 'first',
        showDetail: false,
        name: 'detail',
        total: 0,
        currentPage: 1,
        pageSizes: [10, 20, 50, 100, 200],
        pageSize: 50,
        tableData: [],
      }
    },
    methods: {
      filterIdentifier(value, row){
        return row.identifier === value;
      },
      getDevices(){
        Common.ajax({
          url: '/api/requestParam/device',
          type: 'GET',
          contentType: 'application/json',
          success: (response) => {
            if (response.data && response.data.list) {
              this.deviceOptions = response.data.list;
            }
          },
          error: (err) => {
            this.deviceOptions = [{
              value: 'ALL',
              name: 'ALL',
            }, {
              value: 'iPhone',
              name: 'IOS',
            }, {
              value: 'Android',
              name: 'Android'
            }];
          }
        });
      },
      search(){
        let self = this;
        let dateRange = self.searchParam.dateRange;
        Common.ajax({
          url: "/api/finance/dailyReconciliation",
          type: 'POST',
          contentType: "application/json",
          data: {
            pageIndex: self.currentPage,
            pageSize: self.pageSize,
            beginTime: formatDate(dateRange[0], 'yyyy-MM-dd'),
            endTime: formatDate(dateRange[1], 'yyyy-MM-dd')
          },
          success(response){
            //console.log(response);
            if (response.data && response.data.list) {
              let data = response.data;
              self.tableData = data.list;
              self.total = data.pageUtil.pageCount;
            }
          },
          error(err){
            let error = err.message || err;
            Notification.error({
              title: '错误',
              message: error
            });
          }
        })
      },
      removeLabel(event){
        //console.log(event);
        event.stopPropagation();
        this.activeName = 'first';
        this.showDetail = false;
      },
      handleSizeChange(val) {
        this.pageSize = val;
        //console.log(`每页 ${val} 条`);
        this.search();
      },
      handleCurrentChange(val) {
        this.currentPage = val;
        //console.log(`当前页: ${val}`);
        this.search();
      },
      handleClick(tab, event) {
        let index = tab.index;
        if (index == 0) {
          this.activeName = 'first';
        }
      }
    },
    created(){
      //this.search();
      //console.log('created', this);
    },
    mounted(){
      //
      //console.log('mounted', this);
      this.getDevices();
      this.search();

    },
    beforeDestroy(){

    },
  }
</script>

